html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
    outline: none;
    font-family: Roboto, sans-serif; 
  }
  html { height: 101%; } /* always display scrollbars */
  body { font-size: 62.5%; line-height: 1; font-family: Arial, Tahoma, Verdana, sans-serif; }
  
  article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section { display: block; }
  ol, ul { list-style: none; }
  
  blockquote, q { quotes: none; }
  blockquote:before, blockquote:after, q:before, q:after { content: ''; content: none; }
  strong { font-weight: bold; } 
  
  input { outline: none; }
  
  table { border-collapse: collapse; border-spacing: 0; }
  img { border: 0; max-width: 100%; }
  
  a { text-decoration: none; }
  a:hover { text-decoration: underline; }

body {
    width: 100%;
    background: linear-gradient(270.33deg, #0B1D3F 0.57%, #658BD6 63.59%, #494D73 98.94%);
}

h1 {
    font-size: 36px;
}

h3 {
    font-size: 18px;
}

.container {
    max-width: 1240px;
    width: 100%;
}

.header {
    height: 50px;
    width: 100%;
    display: flex;
    justify-content: center;
    overflow: hidden;
    background: linear-gradient(270deg, #C9A0B0 48.16%, rgba(201, 160, 176, 0) 118.19%);
}

.head__main {
    height: 50px;
    display: flex;
    justify-content: space-between;
}

.main-logo {
    display: flex;
    justify-content: center;
    align-items: center;
}

.main-logo h1 {
    font-weight: 900;
    color: #D6CEF3;
}

.main-con {
    max-width: 730px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.con__items {
    display: flex;
    align-items: center;
}

.item__menu {
    display: flex;
    align-items: center;
    height: 30px;
    padding: 0 25px;
    color: #fff;
    border-width: 0 0 0 2px;
    border-style: solid;
}

.item__menu-end {
    border-width: 0 2px;
}

.menu-bloc {
    color: #fff;
    font-weight: bold;
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    transition: .3s ease;
    cursor: pointer;
}

.item__menu .active {
    color: #95D3EE;
}

.menu-bloc i {
    font-size: 18px;
    margin-right: 5px;
}

.menu-bloc:hover {
    color: #95D3EE;
    text-decoration: none;
}

.con__profile {
    cursor: pointer;
}

.img-con {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    border: 1px solid #5F83CA;
    box-sizing: border-box;
    box-shadow: 0px 0px 8px 1px #6287CF;
    border-radius: 105px;
    transition: .3s ease-out;
}

.user-con .profile-con {
    display: none;
    position: absolute;
    padding: 5px 10px 5px 15px;
    background: rgba(157, 182, 233, 0.8);
}

.img-con:hover {
    box-shadow: 0px 0px 8px 1px #E35656;
}

.user-con:hover .profile-con{
    display: block;
}

.logout p {
    font-size: 12px;
    font-weight: bold;
    color: #fff;
}

.logout p:hover {
    text-decoration: underline;
}

.content-block {
    margin-top: 35px;
    display: flex;
    justify-content: center;
}

.central__con {
    display: flex;
    justify-content: space-between;
}

.content-con {
    max-width: 925px;
    width: 100%;
}

.navigation-con {
    padding: 35px 30px;
    background: rgba(95, 194, 237, 0.11);
    border: 1px solid rgba(101, 139, 214, 0.1);
    box-sizing: border-box;
    box-shadow: 5px 5px 3px rgba(11, 29, 63, 0.35);
    border-radius: 10px;
}

.nav__block {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.nav-item {
    height: 30px;
    margin-bottom: 25px;
    width: 150px;
    padding: 0 25px;
    color: #4366E0;
    background: #9DB6E9;
    border-radius: 13px;
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    transition: .3s ease-out;
    cursor: pointer;
}

.nav-item:hover {
    background: #0240BC;
    color: #9DB6E9;
    box-shadow: 1px 2px 4px rgba(152, 17, 138, 0.55);
}

.nav-txt {
    margin-left: 13px;
}

.nav-txt h5{
    font-size: 12px;
    font-weight: 600;
}


/* -----HOME----- */

.Home {
    max-width: 770px;
    width: 100%;
}

.slide-items {
    display: flex;
    justify-content: space-between;
}

.project__item {
    max-width: 130px;
    width: 100%;
    height: 190px;
    position: relative;
    background: rgba(255, 255, 255, 0.1);
    box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.5);
    border-radius: 20px;
    overflow: hidden;   
    border-top: 1px solid rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(5px);
}

.proj__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 15px;
    padding-bottom: 5px;
}

.proj-img {
    max-width: 60px;
    width: 100%;
    max-height: 60px;
    border-radius: 50%;
    border: 2px solid #5FC2ED;
    box-sizing: border-box;
    box-shadow: 0px 0px 4px 3px rgba(2, 64, 188, 0.22);
}

.proj-img img {
    width: 100%;
}

.proj-stars {
    font-size: 15px;
    display: flex;
    padding-top: 10px;
    color: #E8D000;
}

.proj-user {
    background: linear-gradient(180deg, #5FC2ED 0%, rgba(149, 211, 238, 0) 100%);
    border-radius: 4px; 
    color: rgba(152, 17, 138, 0.8);
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 11px;
    margin: 2px 10px;
    display: flex;
    justify-content: center;
    padding: 5px;
    max-width: 110px;
    width: 100%;
    height: 20px;
    text-shadow: 0px 3px 2px rgb(0 0 0 / 20%);
}

.proj-user h5 {
    padding: 0 2px;
}

.proj__info {
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}

.txt-info {
    line-height: 11px;
    background: #5FC2ED;
    padding: 4px;
    font-size: 10px;
    text-align: center;
    color: #0240BC;
    text-shadow: 0px 3px 2px rgba(0, 0, 0, 0.25);
}

.button-info {
    z-index: 1;
    margin-top: 30px;
    position: absolute;
    width: 36px;
    height: 20px;
    background: #5FC2ED;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    transition: .2s ease-out;
    font-size: 19px;
    z-index: 1;
    display: flex;
    justify-content: center;
    color: #98118A;
}

.bg-info {
    position: absolute;
    width: 128px;
    bottom: 0;
    border: 1px solid #5FC2ED;
    background: #98118A;
    border-bottom-left-radius: 25px;
    border-bottom-right-radius: 25px;
    transition: .2s ease-out;
}
